@import "https://cdn.jsdelivr.net/npm/bulma@0.9.2/css/bulma.min.css";

body {
  background-color: whitesmoke;
  box-sizing: border-box;
}

.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.movie-card {
  border: 1px solid black;
  height: 520px;
  margin: 10px;
  width: 400px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 10px;
}

.movie-card img {
  height: 250px;
  width: 400px;
}

.card-footer {
  align-self: flex-end;
}

.button-box {
  display:flex;
  justify-content: center;
  margin: 20px 0;
}